<script setup>
import { ref, reactive, toRefs, computed, watchEffect } from 'vue';
const props = defineProps({
  colorSelect: Object,
  pixelSize: Number,
});
const { colorSelect, pixelSize } = toRefs(props);
const left = computed(() => colorSelect.value.x);
const top = computed(() => colorSelect.value.y);
</script>
<template>
  <div
    class="PixelHoverHighlight"
    :style="{
      transform: `translate(${left}px, ${top}px`,
      width: pixelSize + 'px',
      height: pixelSize + 'px',
    }"
  ></div>
</template>

<style lang="scss" scoped>
