<script setup>
import { ref, reactive, toRefs } from "vue";
import popupTitle from "@/components/popupTitle.vue";
import { commonStore } from "@/store/common";
import useClipboard from "vue-clipboard3";
import { Toast } from "vant";
import loadingIcon from "./loadingIcon.vue";

const props = defineProps({
  finish: Boolean,
  success: Boolean,
  hash: String,
  type: String,
  text: String,
  title: String,
  btn: String,
  empowerText: String,
  empower: Boolean,
  check: Boolean,
});

const { hash } = toRefs(props);
const emit = defineEmits(["closeFn", "empowerFn", "submit"]);
const common_store = commonStore();
const { toClipboard } = useClipboard();
</script>
<template>
  <div class="bind allFlex column">
    <span
      class="success-btn Wcolor font-Two"
      :class="[success ? 'btnBg' : 'redBg']"
      v-show="finish"
      >{{ success ? $t("success") : $t("fail") }}</span
    >
    <loading-icon class="loading" v-show="!finish && check"></loading-icon>
    <p class="popup-title Wcolor font-Eight font500 center">
      {{ $t(title) }}
    </p>
    <div class="bind_ipt center Wcolor" v-if="!finish">
      <slot></slot>
    </div>

    <div
      class="bind_ipt bgPadding center"
      :class="[success ? 'mainColor font-Ty' : 'redColor hashFont font-Four']"
      v-if="finish"
    >
      {{ success ? $t(text) : hash }}
    </div>
    <div class="bgPadding flex_between">
      <van-button
        type="primary"
        block
        v-if="!empower"
        :disabled="check"
        class="font-Three"
        @click="$emit('empowerFn')"
        >{{ $t(empowerText) }}</van-button
      >
      <van-button
        class="btn font-Three"
        type="primary"
        :disabled="!empower || check"
        block
        @click="$emit('submit')"
        v-show="!finish"
        >{{ $t(btn) }}</van-button
      >
      <van-button
        class="btn font-Three"
        :type="success ? 'primary' : 'danger'"
        block
        v-show="finish"
        @click="$emit('closeFn')"
        >{{ $t("SwapAlert.close") }}</van-button
      >
    </div>
  </div>
</template>

