<script setup>
import { ref, reactive, computed, onBeforeMount, toRefs } from 'vue';
import { hexPalette } from '@/utils/color.js';
import nowColor from './nowColor.vue';
import selectColor from './selectColor.vue';
import { canvasStore } from '@/store/canvas';
const canvasBg = require('@assets/commonImg/bg.png');
const store = canvasStore();
const props = defineProps({
  type: String,
  size: Number,
});
const { type, size } = toRefs(props);
const data = reactive({
  colorClick: {
    x: 0,
    y: 0,
  },
});

const activeColorId = computed(() => store.activeColorId);
const colorArr = [];
const createBox = () => {
  // const len = Math.sqrt(hexPalette.length);
  const len = 8;
  for (let a = 0; a < hexPalette.length; a++) {
    const x = a % len;
    const y = Math.floor(a / len);
    colorArr[y * len + x] = Box(y * len + x, x * size.value, y * size.value, a);
  }
};
const Box = (id, x, y, color) => {
  return {
    id,
    x,
    y,
    color,
  };
};
const boxEvent = (event) => {
  data.colorClick = event.target.attrs;
  if (data.colorClick.colorId >= 0) {
    store.selectColor(data.colorClick.colorId, type.value);
  }
};
onBeforeMount(() => {
  createBox();
});
</script>
<template>
  <div class="color-box">
    <v-stage
      @tap="boxEvent"
      :config="{
        width: size * 8,
        height: size * 2,
      }"
    >
      <v-layer>
        <v-rect
          v-for="(item, key) in colorArr"
          :key="key"
          :x="item.x"
          :y="item.y"
          :width="size"
          :height="size"
          :colorId="item.color"
          :fill="hexPalette[item.color]"
        ></v-rect>
      </v-layer>
    </v-stage>
    <select-color
      v-show="data.colorClick"
      :colorSelect="data.colorClick"
      :pixelSize="size"
    ></select-color>
  </div>
  <now-color :colorId="activeColorId"></now-color>
</template>

<style lang="scss" scoped>
.color-box {
  display: inline-block;
  position: relative;
  margin: 0 auto;
}
</style>
