<script setup>
import { ref, reactive } from 'vue';
let autoplay = ref(false);
const videoRef = ref(null);
const changeVideoStatus = () => {
  if (videoRef.value.paused) {
    videoRef.value.play();
  } else {
    videoRef.value.pause();
  }
};
</script>
<template>
  <div class="video">
    <!-- <p class="lightBlue font-Five title">{{ $t('Home.title1') }}</p> -->

    <div class="video-box" @click="changeVideoStatus">
      <img
        src="@assets/homeImg/open.png"
        alt=""
        class="positionAll open_icon"
        v-if="!autoplay"
      />
      <video
        @play="autoplay = true"
        @pause="autoplay = false"
        @ended="autoplay = false"
        ref="videoRef"
        preload="auto"
        class="video-mp"
        poster="../../../assets/homeImg/bg5.png"
      >
        <source src="https://video-sky.netlify.app/video.mp4" />
      </video>
    </div>

