<script setup>
import {
  ref,
  reactive,
  onBeforeMount,
  computed,
  watchEffect,
  watch,
  toRefs,
} from "vue";
import { hexPalette } from "@/utils/color.js";
// import cjmlBox from '@/utils/cjml.js';
import { canvasStore } from "@/store/canvas";
import { commonStore } from "@/store/common";
import { useI18n } from "vue-i18n";
import { Toast } from "vant";
// const canvasBg = require('@assets/homeImg/cjml.webp');
const canvasBg = require("@assets/commonImg/bg.png");
// import selectPixelList from "./selectPixelList.vue";
const { t } = useI18n();
const data = reactive({
  show: false,
  pixelSize: 7,
  colorSize: 15,
  scale: 1,
  pageMax: 35,
  mousePosition: null,
  configKonva: {
    width: 350,
    height: 350,
  },
  pixelClick: null,
  colorHovered: null,
  checkColorId: 0,
  boxArr: [],
  check: false,
  config: {
    name: "layerImage",
    x: 0,
    y: 0,
    scaleX: 1,
    scaleY: 1,
    width: 350,
    height: 350,
  },
});
const stage = ref(null);

const props = defineProps({
  finish: Boolean,
});
const { finish } = toRefs(props);
const emit = defineEmits(["updataFinish"]);
const groupBox = ref(null);
const canvas_store = canvasStore();
const common_store = commonStore();
let colorArr = [];
let tokenArr = [];
const page = computed(() => canvas_store.page);
// const cjml = computed(() => cjmlBox[page.value]);
const activeColorId = computed(() => canvas_store.activeColorId);
const createBox = () => {
  for (let i = 0; i < 50; i++) {
    for (let n = 0; n < 50; n++) {
      data.boxArr[i * 50 + n] = Box(
        i * 50 + n,
        n * 7,
        i * 7,
        colorArr[i * 50 + n]
      );
    }
  }
};
const Box = (id, x, y, color) => {
  return {
    id,
    x,
    y,
    color,
    check: false,
    colorRgb: hexPalette[color - 1],
    // colorRgb: hexPalette[color],
  };
};
const randomHexColor = () => {
  return Math.floor(Math.random() * 255 + 1);
};
// const boxEvent = (event) => {
//   if (activeColorId.value == 0) {
//     Toast('请先选择颜色');
//     return;
//   }
//   data.pixelClick = event.target.attrs;
//   if (canPaintHoveredPixel()) {
//     Toast('此像素不能购买');
//     return;
//   }
//   const params = {
//     newPixel: data.pixelClick,
//     colorId: activeColorId.value,
//   };

//   if (canvas_store.pixelExists(params)) {
//     data.boxArr[data.pixelClick.boxId].color = activeColorId.value;
//     data.boxArr[data.pixelClick.boxId].check = true;
//   } else {
//     data.boxArr[data.pixelClick.boxId].color = 0;
//     data.boxArr[data.pixelClick.boxId].check = false;
//   }
// };
const canPaintHoveredPixel = () => {
  return (
    data.boxArr[data.pixelClick.boxId] &&
    data.boxArr[data.pixelClick.boxId].color != 0 &&
    !data.boxArr[data.pixelClick.boxId].check
  );
};
const dragmove = () => {
  const dom = groupBox.value.getNode();
  var x = dom.width();
  var y = dom.height();
  // var radius = 10;
  // const pos = groupBox.value.getNode().absolutePosition();
  // const scale =
  //   radius / Math.sqrt(Math.pow(pos.x - x, 2) + Math.pow(pos.y - y, 2));

  // if (scale < 1) {
  //   groupBox.value.getNode().x(Math.round((pos.x - x) * scale + x));
  //   groupBox.value.getNode().y(Math.round((pos.y - y) * scale + y));
  // }
  if (dom.x() > 0) {
    dom.x(0);
  }
  if (dom.x() < -(x * (data.scale - 1))) {
    dom.x(-(x * (data.scale - 1)));
  }
  if (dom.y() > 0) {
    dom.y(0);
  }
  if (dom.y() < -(y * (data.scale - 1))) {
    dom.y(-(y * (data.scale - 1)));
  }
};

const getMaxPage = async () => {
  const page = await common_store.allMethods.getCanvasCount().call();
  canvas_store.changePage(page - 1);
  getPixelList(page - 1);
};

const getPixelList = async (canvasId) => {
  try {
    const res = await common_store.allMethods.getCanvasBitmap(canvasId).call();
    console.log(res);
    tokenArr = res.tokenid_;
    colorArr = res.color_;
    createBox();
    data.check = false;
    emit("updataFinish");
  } catch (error) {
    data.check = false;
    // Toast(error.message);
  }
};
const changePage = (sum) => {
  if (data.check) {
    return;
  }
  if (+page.value == 0 && sum < 0) {
    Toast(t("lastPage"));
    return;
  }
  if (+page.value == +data.pageMax && sum > 0) {
    Toast(t("nextPage"));
    return;
  }
  data.check = true;
  const num = page.value + sum;
  canvas_store.changePage(num);
  getPixelList(page.value);
};
watch(
  () => finish.value,
  (val) => {
    if (val && common_store.address) {
      getPixelList(page.value);
    }
  }
);
watchEffect(() => {
  if (common_store.address) {
    getMaxPage();
  }
});
</script>
<template>
  <div class="pixel-item">
    <div class="left-canvas centerFlex">
      <v-stage
        ref="stage"
        :style="{
          width: '350px',
          background: `url(${canvasBg})`,
        }"
        :config="data.configKonva"
      >
        <v-layer>
          <v-group
            :config="data.config"
            :centeredScaling="true"
            ref="groupBox"
            :style="{}"
          >
            <!-- <v-rect
              v-for="(item, key) in data.boxArr"
              :key="key"
              :x="item.x"
              :y="item.y"
              :width="data.pixelSize"
              :height="data.pixelSize"
              :fill="hexPalette[item.color]"
              :colorId="item.color"
              :boxId="item.id"
              :check="item.check"
            >
            </v-rect> -->
            <v-rect
              v-for="(item, key) in data.boxArr"
              :key="key"
              :x="item.x"
              :y="item.y"
              :width="data.pixelSize"
              :height="data.pixelSize"
              :fill="item.colorRgb"
              :colorId="item.color - 1"
              :boxId="item.id"
              :check="item.check"
              stroke="#000000"
              :strokeWidth="0.05"
            >
            </v-rect>
          </v-group>
        </v-layer>
      </v-stage>
    </div>
  </div>
  <div class="flex_between page-btn padding">
    <img
      src="@assets/homeImg/left_icon.png"
      alt=""
      class="row_icon"
      @click="changePage(-1)"
    />
    <span class="font-Tf Wcolor Bold">{{ +page + 1 }}</span>
    <img
      src="@assets/homeImg/right_icon.png"
      alt=""
      class="row_icon"
      @click="changePage(1)"
    />
  </div>
</template>

