<script setup>
import { ref, reactive, onBeforeMount, watchEffect, onMounted } from 'vue';
import { getMarketSell } from '@/http/api';
import { commonStore } from '@/store/common';
import pixelItem from '@/components/pixelItem.vue';
import { useRouter } from 'vue-router';
const common_store = commonStore();
const data = reactive({
  list: [],
  counts: [],
  timer: null,
  time: 1,
  count: 0,
  time2: 0,
});
const dom = ref(null);
const getMarketList = () => {
  getMarketSell(0, 'selltime').then((res) => {
    data.list = res.data.results;
    data.count = res.data.results.length;
    const indexArr = [];
    data.list.forEach((item) => {
      const tokenid = item?.tokenid || item?.tokenId;
      indexArr.push(tokenid);
    });

    if (data.list.length > 2) {
      data.list = data.list.concat(res.data.results);
    }

    // getTimes(indexArr);
  });
};
// const getTimes = async (indexArr) => {
//   data.counts = await common_store.marketMethods.listNftCount(indexArr).call();
//   if (data.list.length > 2) {
//     data.counts.concat(data.counts);
//   }
// };
onBeforeMount(() => {
  getMarketList();
});
watchEffect(() => {
  if (common_store.address) {
    common_store.initMarketWeb3();
  }
});
const router = useRouter();
const toDetails = (item) => {
  if (common_store.address) {
    router.push({
      path: '/marketDetails',
      query: item,
    });
  } else {
    common_store.setLinkShow(true);
  }
};
let scrollWidth = ref(0);

const setTime = () => {
  data.time2++;
  data.time = data.time2 % (data.count * 2 * 60);
  if (data.time2 == data.count * 2 * 60) {
    data.time2 = 0;
  }
  requestAnimationFrame(setTime);
};
onMounted(() => {
  setTime();
  watchEffect(() => {
    scrollWidth.value = 215 * data.list.length;
  });
});
</script>

<template>
  <div class="market-box">
    <div
      class="market-box_list"
      ref="dom"
      v-if="data.list > 2"
      :style="{
        'transition-duration':
          data.time <= 120 ? '0s' : data.count * 2 - 2 + 's',
        transform:
          data.time <= 120
            ? 'translateX(0)'
            : `translateX(${-(scrollWidth / 2)}px)`,
      }"
    >
      <div
        v-for="(item, key) in data.list"
        :key="key"
        style="width: 200px; margin-left: 15px"
        class="market-box_item listBg"
      >
        <pixel-item
          :pixelInfo="item"
          name="market"
          type="home"
          class="item"
          :count="0"
          @click="toDetails(item)"
        ></pixel-item>
      </div>
    </div>
    <div class="market-box_list" v-else>
      <div
        v-for="(item, key) in data.list"
        :key="key"
        style="width: 200px; margin-left: 15px"
        class="market-box_item listBg"
      >
        <pixel-item
          :pixelInfo="item"
          name="market"
          type="home"
          class="item"
          :count="0"
          @click="toDetails(item)"
        ></pixel-item>
      </div>
    </div>
  </div>
</template>

