      .mint(activeColorId.value + 1)
      .send({ from: common_store.address });
    emit("setFinish");
    data.finish = true;
    data.success = true;
    data.check = false;
    data.hash = res1.transactionHash;
    common_store.addHash(data.hash, 0);
    canvas_store.clearColor();
