  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  try {
    const res = await swap_store.tokenMethods
      .approve(swap_store.nftSpender, MaxUint256)
      .send({ from: common_store.address });
    swap_store.setEmpower();
  } catch (error) {
