<script setup>
import { ref, reactive, onBeforeMount, computed } from "vue";
import { hexPalette } from "@/utils/color.js";
import colorPixel from "@/components/colorPixel.vue";
import hasColor from "./hasChangeColor.vue";
import { MaxUint256 } from "@ethersproject/constants";
import popupTitle from "@/components/popupTitle.vue";
import { canvasStore } from "@/store/canvas";
import { swapStore } from "@/store/swap";
import { commonStore } from "@/store/common";
import { Toast } from "vant";
const canvasBg = require("@assets/commonImg/bg.png");

const store = canvasStore();
const swap_store = swapStore();
const common_store = commonStore();
const data = reactive({
  colorSize: 15,
  colorKonva: {
    width: 240,
    height: 240,
  },
  empower: false,
  check: false,
  colorClick: {
    x: 0,
    y: 0,
  },
});
const activeColorId = computed(() => store.activeColorId);
const colorArr = [];
const emit = defineEmits(["updata", "close"]);
const createBox = () => {
  const len = Math.sqrt(hexPalette.length);
  for (let a = 0; a < hexPalette.length; a++) {
    const x = a % len;
    const y = Math.floor(a / len);
    colorArr[y * len + x] = Box(y * len + x, x * 15, y * 15, a);
  }
};
const Box = (id, x, y, color) => {
  return {
    id,
    x,
    y,
    color,
  };
};
const boxEvent = (event) => {
  if (event.target.attrs.colorId == activeColorId.value) {
    return;
  }

  data.colorClick = event.target.attrs;
  store.selectColor(data.colorClick.colorId);
};
onBeforeMount(() => {
  createBox();
});
const toMint = async () => {
  // if (activeColorId.value == 0) {
  //   Toast(t("Home.alert1"));
  //   return;
  // }
  if (data.check) {
    return;
  }
  data.check = true;
  // await swap_store.getTokenMethods();
  // if (!swap_store.hasEmpower) {
  //   common_store.setEmpowerType(1);
  //   common_store.showEmpower(true);
  //   return;
  // }
  try {
    const res1 = await common_store.allMethods
      .mint(activeColorId.value + 1)
      .send({ from: common_store.address });
    emit("updata");
    data.check = false;
    common_store.addHash(res1.transactionHash, 0);
  } catch (error) {
    data.check = false;
    data.show = false;
    if (error.message) {
      Toast(error.message);
    } else {
      Toast(error);
    }
  }
};
const empower = async () => {
  if (data.check) {
    return;
  }
  data.check = true;
  if (!swap_store.tokenEmpower) {
    await swap_store.getTokenMethods();
  }
  try {
    const res = await swap_store.tokenMethods
      .approve(swap_store.nftSpender, MaxUint256)
      .send({ from: common_store.address });
    swap_store.setEmpower();
    data.check = false;
  } catch (error) {
    data.check = false;
    if (error.message) {
      Toast(error.message);
    } else {
      Toast(error);
    }
  }
};
</script>
<template>
  <div
    class="color-box"
    :class="[swap_store.hasEmpower ? 'noempowerBox' : 'empowerBox']"
  >
    <div class="color-title">
      <p class="Wcolor font-Six Bold center">
        {{ $t("Home.text4") }}
      </p>
      <img
        src="@assets/accountImg/close_icon.png"
        alt=""
        class="clsoe_icon centerAll"
        @click="$emit('close')"
      />
    </div>
    <div class="color-main">
      <div class="color-box_change">
        <color-pixel :size="store.colorSize"></color-pixel>
        <now-color :colorId="activeColorId"></now-color>
      </div>
      <van-button
        type="primary"
        :disabled="data.check"
        v-if="!swap_store.hasEmpower"
        block
        plain
        @click="empower"
        class="empower-btn mainColor"
        >{{
          data.check ? $t("EmpowerAlert.btn1") : $t("EmpowerAlert.btn4")
        }}</van-button
      >
      <van-button
        type="primary"
        :disabled="!swap_store.hasEmpower || data.check"
        block
        @click="toMint"
        class="empower-btn"
        >{{
          swap_store.hasEmpower && data.check
            ? $t("SwapAlert.btn3")
            : $t("Home.title")
        }}</van-button
      >
    </div>

    <!-- <p class="font-Four color-title Wcolor">{{ $t('Home.text5') }}</p>
    <has-color></has-color> -->
  </div>
</template>

